@use "../styles/variables" as *;

.layoutContainer {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 10rem 1fr;
  height: 100vh;
  background-color: $light-blue;

  .main {
    display: grid;
    grid-template-columns: subgrid;
    grid-column: 1/13;
    height: calc(100vh - 10rem);
    overflow: hidden;
  }
}
